* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@font-face {
  font-family: "proxima-nova";
  src: url("../fonts/ProximaNova-Regular.eot");
  src: url("../fonts/ProximaNova-Regular.woff") format("woff"), url("../fonts/ProximaNova-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "proxima-nova";
  src: url("../fonts/ProximaNova-Bold.eot");
  src: url("../fonts/ProximaNova-Bold.woff") format("woff"), url("../fonts/ProximaNova-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}

h1 {
  font-size: 2.5rem;
  text-align: center;
  margin-bottom: 1rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.6rem;
}

h4 {
  font-size: 1.2rem;
}

p {
  text-indent: 1rem;
  text-align: justify;
  text-justify: auto;
  padding: 2rem 1.5rem;
  color: #858484;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: bold;
  padding-top: 1rem;
}

.to-upper {
  text-transform: uppercase;
}

.navbar {
  position: absolute;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #1c1c1c;
  top: 0px;
  position: fixed;
  width: 100vw;
  height: 43px;
  z-index: 3;
}

.navbar-hidden {
  display: none;
}

.navbar-logo a {
  text-decoration: none;
  color: white;
  font-weight: 600;
  font-size: 1.5rem;
}

.navbar-list {
  position: absolute;
  top: -800px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  z-index: 1;
  background: black;
  visibility: hidden;
  list-style-type: none;
  transition: all 0.5s ease-in-out;
}

.navbar-list.open {
  visibility: visible;
  transform: translateY(843px);
}

.navbar-item {
  font-weight: bold;
  margin-bottom: 50px;
  font-size: 1.8rem;
  opacity: 90%;
  transform: translateX(150vw);
  transition: all 0.3s ease-in;
}

.navbar-item > a {
  text-decoration: none;
  color: inherit;
  -webkit-tap-highlight-color: transparent;
}

.navbar-item.open {
  transform: translateX(0);
}

.navbar-item.open::before {
  width: 60%;
}

.navbar-item::before {
  content: "";
  position: absolute;
  width: 1%;
  border-top: 2px white solid;
  transition: all 2s ease-in-out;
}

.navbar-icon {
  position: relative;
  top: 24px;
  display: block;
  background: white;
  height: 2px;
  width: 27px;
  margin: 0 auto;
  transition: all 0.5s ease-in-out;
}

.navbar-icon::before {
  content: "";
  background: white;
  position: absolute;
  top: 6px;
  right: 0px;
  height: 2px;
  width: 27px;
  transition: all 0.5s ease-in-out;
}

.navbar-icon::after {
  content: "";
  background: white;
  position: absolute;
  top: -6px;
  right: 0px;
  height: 2px;
  width: 27px;
  transition: all 0.5s ease-in-out;
}

.navbar-icon.open {
  transform: rotate(720deg);
  background: transparent;
}

.navbar-icon.open::after {
  transform: rotate(45deg) translate(3.6px, 6px);
}

.navbar-icon.open::before {
  width: 28px;
  transform: rotate(-45deg) translate(2.7px, -4px);
}

.navbar-icon-bg {
  position: absolute;
  top: 15px;
  right: 8%;
  z-index: 3;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  background: #858484;
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
}

.navbar-item:nth-child(1) {
  transition-delay: 0.25s;
}

.navbar-item:nth-child(2) {
  transition-delay: 0.35s;
}

.navbar-item:nth-child(3) {
  transition-delay: 0.45s;
}

.navbar-item:nth-child(4) {
  transition-delay: 0.55s;
}

body {
  background: #1c1c1c;
  color: white;
  font-family: "proxima-nova", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

button {
  font-family: "proxima-nova", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  display: block;
  position: relative;
  color: white;
  font-weight: bold;
  padding: 8px 16px;
  background: transparent;
  border: none;
  outline: none;
  -webkit-tap-highlight-color: transparent;
  cursor: pointer;
}

button:focus {
  border: none;
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

button::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  border: 1px solid white;
  transform: skewX(-15deg);
  transition: all 0.5s ease-in-out;
}

button:hover::before {
  transform: skewX(20deg);
}

#camera-4k-act {
  height: 40px;
  width: 20px;
  padding: 0;
  margin-bottom: 15px;
  animation: camera-action 0.8s infinite alternate;
}

.camera {
  display: inline-block;
  animation: camera 0.8s infinite alternate;
}

@keyframes camera-action {
  0% {
    transform: translateX(-10px);
  }
  100% {
    transform: translateX(0px);
  }
}

@keyframes camera {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0px);
  }
}

.sand-watch {
  animation: sand-watch 4s infinite alternate;
}

@keyframes sand-watch {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.stop-w {
  display: inline-block;
  position: relative;
}

.stop-w-outer {
  animation: stop-w-outer 2s infinite linear;
}

.stop-w-inner {
  animation: stop-w-inner 2s infinite linear;
  position: absolute;
  bottom: 11px;
  left: 10px;
}

@keyframes stop-w-inner {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes stop-w-outer {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(-360deg);
  }
}

.carousel {
  position: relative;
  position: relative;
  align-items: center;
}

.carousel-indicators {
  top: 0;
  bottom: unset;
  right: unset;
  left: 50%;
  transform: translate(-50%, 0);
  width: auto;
  margin: 0 auto;
  padding: 0;
}

.carousel-indicators > li {
  height: 15px;
  width: 16px;
  border-radius: 50%;
  margin: 0.3rem;
}

.carousel-arrow {
  font-size: 4rem;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  margin: 0 3rem;
  color: white;
}

.carousel-arrows {
  display: none;
}

.carousel-arrow--right {
  right: 0;
}

.play-btn {
  all: unset;
  position: absolute;
  left: 50%;
  top: 50%;
  outline: none;
  -webkit-tap-highlight-color: transparent;
  height: 85px;
  width: 85px;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  font-size: 3rem;
  z-index: 9999;
  padding: 10px;
  background: #ffffff;
  visibility: hidden;
}

.play-btn-outline {
  visibility: hidden;
  position: absolute;
  top: -97.5px;
  left: -97.5px;
}

.play-btn > ion-icon {
  position: relative;
  left: 16px;
  top: 11px;
  font-size: 4rem;
  color: #000000;
}

.play-btn::before {
  content: none;
}

.play-btn:focus {
  border: none;
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

#inner-circle {
  opacity: 50%;
  border-radius: 100;
}

#outer-circle {
  opacity: 70%;
}

.playBtn > svg {
  position: absolute;
  z-index: 9999;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.custom-controls {
  display: none;
  align-items: center;
  justify-content: space-between;
  width: fit-content;
  background: #858484;
  padding: 15px 0;
  border-radius: 45px;
  opacity: 30%;
  transition: all 0.5s ease-in-out;
  position: absolute;
  bottom: 5%;
  left: 50%;
  transform: translate(-50%, 0);
  z-index: 9999;
}

.custom-controls:hover {
  opacity: 100%;
}

.custom-controls > button {
  display: inline-block;
  background: #ffffff;
  color: #000000;
  width: 64px;
  height: 64px;
  border-radius: 50%;
  font-size: 1.2rem;
  padding: 20px 2px;
  margin: 0 20px;
}

.custom-controls > button::before {
  content: none;
}

.custom-controls-timeline {
  position: relative;
  display: inline-block;
  height: 50px;
  width: 300px;
  background: #ffffff;
  border-radius: 10px;
}

.custom-controls-timeline-time {
  position: absolute;
  top: 50%;
  left: 10%;
  transform: translate(0, -50%);
  color: red;
  z-index: 1;
}

.custom-controls-timeline-line {
  position: absolute;
  top: 0;
  left: 0;
  background: #1c1c1c;
  height: 50px;
  width: 0px;
  border-radius: 10px;
}

.custom-controls-vol {
  position: relative;
}

.custom-controls-vol > span {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 50%;
  display: inline-block;
  width: 64px;
  height: 64px;
  background: transparent;
  z-index: 9999;
}

.custom-controls-vol:hover .custom-controls-vol-wrapper {
  opacity: 100;
}

.custom-controls-vol-wrapper {
  position: absolute;
  left: -43px;
  top: -112px;
  transform: rotate(270deg);
  z-index: 9999;
  background: #ffffff;
  display: inline-block;
  padding: 10px;
  height: 64px;
  border-radius: 30px;
  opacity: 0;
  transition: all 0.5s ease-in-out;
}

.custom-controls-vol-wrapper > input {
  transform: translate(0, 10px);
}

.custom-controls-vol-wrapper:hover {
  opacity: 100;
}

.greetings {
  height: 100vh;
  background-image: url(../img/bg-sm.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
}

.greetings-para {
  text-indent: 1rem;
  padding: 0 8% 1rem 8%;
  color: white;
}

.greetings-heading {
  margin-top: 3rem;
  max-width: 1200px;
  letter-spacing: 0.1rem;
}

.container-fluid {
  padding: 0;
}

.first {
  height: auto;
  background: linear-gradient(#1c1c1c, #252525);
  margin-top: 1rem;
  padding-bottom: 2rem;
}

.first-heading {
  position: relative;
  top: -45px;
  margin-left: 2.5rem;
}

.first-image-container {
  text-align: right;
}

.first-btn {
  margin: 0 auto;
}

.third {
  background: linear-gradient(#1c1c1c, #252525);
  margin-top: 1rem;
}

.third-heading-h2 {
  padding-top: 1rem;
  margin-left: 2.5rem;
}

.third > article > h4,
.third img {
  padding: 0 1.5rem;
  display: inline;
}

.third-article1, .third-article3 {
  text-align: right;
  margin: 0;
}

.third-article2 {
  text-align: left;
  margin: 0;
}

.third-article-para {
  width: 75vw;
  display: inline-block;
  margin-right: 0;
}

.fourth {
  position: relative;
  padding-bottom: 1rem;
}

.fourth-heading {
  padding: 0 5%;
  text-align: center;
  max-width: 900px;
  margin: 0 auto;
  margin-top: 30px;
}

.fourth-list {
  float: left;
  text-align: right;
}

.fourth-list:nth-child(2) {
  padding-bottom: 2rem;
}

.fourth-list > li {
  text-shadow: 0px 0px 5px white;
  display: block;
  font-size: 1.1rem;
  font-weight: bolder;
  color: #000000;
  margin-bottom: 10px;
}

.fourth-list > li > strong {
  font-weight: bold;
  text-transform: uppercase;
  color: white;
}

.fourth-list > .container {
  padding-bottom: 1rem;
}

.fourth-image {
  position: absolute;
}

.fifth {
  padding-top: 50px;
  background: white;
  clear: both;
}

.fifth-heading {
  text-align: center;
  color: #1c1c1c;
  padding: 0 10%;
}

.sixth-heading {
  text-align: center;
  padding-top: 2rem;
}

.sixth-para {
  text-align: center;
}

.footer {
  text-align: right;
}

.footer-social {
  display: block;
}

.footer-social > li {
  display: inline;
}

.footer-social > li > i {
  padding: 11px 0;
  text-align: center;
  color: black;
  background-color: white;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.footer-addition {
  display: inline-block;
}

.footer > div > ul,
.footer > ul {
  list-style: none;
}

.footer > div > ul > li,
.footer > ul > li {
  margin: 15px 10px;
  color: #858484;
}

.footer > div > ul > li > a,
.footer > ul > li > a {
  text-decoration: none;
  color: #858484;
}

.footer-contact-number {
  color: white;
}

.footer-contact-email {
  text-decoration: underline !important;
}

/* Extra Small Devices, Phones */
@media only screen and (min-width: 576px) {
  .greetings {
    height: 100vh;
    background-image: url(../img/bg.png);
  }
  .greetings-heading {
    font-size: 3rem;
    letter-spacing: 0.2rem;
  }
  .fourth-list > li {
    text-shadow: none;
    text-align: right;
    color: #858484;
    font-weight: lighter;
  }
  .fourth-para {
    text-align: center;
  }
  .footer-addition > li {
    display: inline-block;
  }
  .footer ul {
    margin: 1rem 0;
  }
}

/* Small Devices, Tablets */
@media only screen and (min-width: 768px) {
  p {
    font-size: 1.1rem;
  }
  header {
    position: absolute;
  }
  .navbar {
    all: unset;
  }
  .navbar-lg {
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    flex-flow: row wrap;
  }
  .navbar-logo {
    display: inline-block;
  }
  .navbar-hidden {
    display: inline-block;
    order: 2;
  }
  .navbar-hidden > a > i {
    padding: 0 30px 0 40px;
    color: white;
  }
  .navbar-hidden > a,
  .navbar-hidden > a > img,
  .navbar-hidden > button {
    display: inline-block;
  }
  .navbar-icon-bg {
    display: none;
  }
  .navbar-list {
    all: unset;
    display: flex;
    justify-content: space-evenly;
    margin: 1rem;
    order: 3;
    flex-basis: 100%;
  }
  .navbar-item {
    all: unset;
    text-transform: uppercase;
    font-family: "proxima-nova", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bold;
  }
  .navbar-item::before {
    content: none;
  }
  .greetings-heading {
    font-size: 5rem;
  }
  .first-heading {
    top: 0;
    margin-top: 6rem;
  }
  .first-image {
    width: 50%;
    float: right;
  }
  .first-para {
    text-align: left;
    font-size: 1rem;
  }
  .carousel-indicators {
    top: 0;
  }
  .carousel-indicators > li {
    height: 20px;
    width: 22px;
    margin: 0.3rem;
  }
  .carousel-arrows {
    display: inline;
  }
  .third {
    text-align: center;
  }
  .third-article1, .third-article2, .third-article3 {
    display: inline-block;
    text-align: left;
  }
  .third-article1 > h4, .third-article2 > h4, .third-article3 > h4 {
    margin-top: 1rem;
  }
  .third-article-para {
    font-size: 0.9rem;
    line-height: 1.3rem;
    width: auto;
    text-align: left;
    padding-left: 0;
    text-indent: 0;
  }
  .fourth-image {
    width: 50%;
  }
  .fourth-list > li {
    text-shadow: none;
    text-align: left;
    color: #858484;
    font-weight: lighter;
    margin: 2rem 0;
  }
  .fourth-para {
    text-align: center;
  }
  .fourth-image {
    width: 45%;
  }
}

/* Medium Devices, Desktops */
@media only screen and (min-width: 992px) {
  .greetings-heading {
    font-size: 7rem;
    letter-spacing: 0.3rem;
  }
  .third-article-para {
    font-size: 1rem;
    line-height: 1.3rem;
  }
  .fourth-image {
    width: 38%;
  }
  .footer-addition {
    text-align: left;
  }
  .footer-contact {
    display: flex;
    justify-content: space-between;
  }
  .footer-contact > li {
    display: inline-block;
  }
  .footer ul {
    margin: 2rem 0;
  }
  .custom-controls {
    display: flex;
  }
  .play-btn {
    visibility: visible;
  }
  .play-btn-outline {
    visibility: visible;
  }
}

/* Large Devices, Wide Screens */
@media only screen and (min-width: 1200px) {
  button {
    padding: 10px 25px;
  }
  .navbar-logo > a {
    font-size: 2rem;
    padding-left: 2rem;
  }
  .navbar-list {
    order: 2;
    flex-basis: auto;
  }
  .navbar-item {
    margin: 0 1rem;
  }
  .navbar-hidden {
    order: 3;
  }
  .navbar-hidden > a > i {
    padding: 0 30px 0 40px;
    color: white;
  }
  .navbar-lg {
    order: 1;
  }
  p {
    font-size: 1.4rem;
  }
  .greetings-heading {
    font-size: 7rem;
    letter-spacing: 0.4rem;
  }
  .first-heading {
    font-size: 2.5rem;
    margin-left: 0;
  }
  .first-para {
    font-size: 1.2rem;
    line-height: 2rem;
    text-indent: 0;
    margin-left: 0;
    padding-left: 0;
  }
  .first-btn {
    margin: 0;
    padding: 15px 30px;
    font-size: 1.2rem;
  }
  .carousel-indicators {
    top: 3%;
  }
  .carousel-indicators > li {
    height: 25px;
    width: 28px;
    margin: 0.6rem;
  }
  .third-article-para {
    font-size: 1.1rem;
    line-height: 1.5rem;
  }
  .fourth-image {
    width: 30%;
  }
}
